import logo from './logo.svg';
import './App.css'; 
import "antd/dist/antd.css";
import { Radio } from 'antd';
// import Menu from './menu.js'
import Likes from './like/likes.js'
import Prob from './prob/prob.js'
import  { Component } from 'react';


export default class App extends Component {
  constructor(...args) {
    super(...args);
    this.state = {
      showingTab:'like'
    };
   }
   showConponent = (v) => {
    if(v.target.value ==='a'){
      this.setState({
        showingTab:'like'
      });
    }else if(v.target.value ==='b'){
      this.setState({
        showingTab:'prob'
      })
    }
   }
  //  hideConponent = () => {
  //   this.setState({
  //    isShowList3: false,
  //   });
  //  }
   render() {
    return (
    <div className="App">
      {/* <Menu></Menu> */}
      <div style={{textAlign:'left',marginBottom:10,}}>
        <Radio.Group defaultValue="a" buttonStyle="solid" onChange={value=>this.showConponent(value)}>
          <Radio.Button value="a" ></Radio.Button>
          <Radio.Button value="b"></Radio.Button>
          <Radio.Button value="d" disabled></Radio.Button>x 
        </Radio.Group>
      </div>
      { this.state.showingTab =='like' ? <Likes></Likes>
      // <List3 hideConponent={this.hideConponent} />
     : this.state.showingTab =='prob' ? <Prob></Prob>
     : null }
      <span></span>
    </div>
  );
  }
}
